import React from "react";

import User from "./components/User";
import user from "./tasks/user.json";

import Statistics from "./components/Statistics";
import statisticalData from "./tasks/statistical-data.json"

const App = () => {
  return (
      <>
<User
  name={user.name}
  tag={user.tag}
  location={user.location}
  avatar={user.avatar}
  stats={user.stats}
      />;

<Statistics
  title="Upload stats"
  stats={statisticalData} 
  />;

 <Statistics
   stats={statisticalData} />; 
  </>
  );
};

export default App;